import React, { Fragment } from 'react';
import { useHistory } from 'react-router-dom';
import "../../assets/scss/err404.scss";


function Err404() {
  let history = useHistory();
  const redirect = () => {
    history.push('/home')
  }

  return (
    <Fragment>
      <div className="center">
        <div className="error">
          <div className="number">4</div>
          <div className="illustration">
            <div className="circle"></div>
            <div className="clip">
              <div className="paper">
                <div className="face">
                  <div className="eyes">
                    <div className="eye eye-left"></div>
                    <div className="eye eye-right"></div>
                  </div>
                  <div className="rosyCheeks rosyCheeks-left"></div>
                  <div className="rosyCheeks rosyCheeks-right"></div>
                  <div className="mouth"></div>
                </div>
              </div>
            </div>
          </div>
          <div className="number">4</div>
        </div>

        <div className="text">Oops. A Pagina que você está procurando não existe.</div>
        <button className="button" onClick={redirect} >Voltar a Home</button>

      </div>
    </Fragment>
  );
};

export default Err404;
